import React from 'react';
import style from './Inform.module.scss';
import styleContainer from "../common/styles/Container.module.css";
import Title from "../common/components/title/Title";
import {Fade} from "react-awesome-reveal";


const Inform = () => {
    return (
        <div className={style.contactsCore}>
            <Fade cascade>
                <div className={`${styleContainer.container} ${style.contactsContainer}`}>
                    <Title text={"Information about me"}/>
                    <p className={style.paragraph}>1. Меня зовут Илья Казак, мне 25 лет</p>
                    <p className={style.paragraph}>2. Номер телефона: +375-25-708-31-94</p>
                    <p className={style.paragraph}>3. Образование: Минский государственный колледж электроники</p>
                    <p className={style.paragraph}>4. Доп.курсы: Айти Инкубатор:React,Redux</p>
                    <p className={style.paragraph}>5. Ваш курс мне очень интересен, потому что я уже учусь на таком же направлении и очень хотелось бы улучшить свое понимание JS, так же получить работу в этой сфере.
                    Изменить свою жизнь, так же уверен что смог бы быть полезен и вам. Надеюсь я вам подойду)</p>


                </div>
            </Fade>
        </div>
    );
};

export default Inform;
